.preloader {
  z-index: 1;
  cursor: pointer;
  background: rgba(226, 226, 229, .455);
  border: none;
  outline: none;
  padding: 1.25rem;
  transition: all .2s ease-in-out;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
}

.loaded_hiding .preloader {
  opacity: 0;
  transition: opacity .3s;
}

.loaded .preloader {
  display: none;
}

.loading .preloader {
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  width: 80px;
  height: 80px;
  border-top: 7px solid var(--spinner-border-top-color);
  border-right: 7px solid rgba(0, 0, 0, 0);
  border-radius: 80%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

.loader:after {
  content: "";
  width: 80px;
  height: 80px;
  border-bottom: 7px solid rgba(0, 0, 0, 0);
  border-left: 7px solid rgba(12, 12, 11, .84);
  border-radius: 80%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(172, 171, 171, .663);
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

body {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

body::-webkit-scrollbar {
  display: none;
}

.backdrop {
  opacity: 1;
  transition: opacity var(--animation), visibility var(--animation);
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.5);
}

.modal {
  opacity: 1;
  transition: opacity var(--animation), transform var(--animation);
  max-width: 100%;
  max-height: 100%;
  width: 280px;
  font-family: var(--main-font-family);
  color: var(--primary-text-color);
  background-color: var(--buttons-modal-bg-color);
  scrollbar-width: none;
  -ms-overflow-style: none;
  margin-left: auto;
  margin-right: auto;
  padding: 48px 20px 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16667;
  position: absolute;
  top: 10px;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%)scale(1);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 704px;
    padding: 40px 72px 64px 36px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -45%)scale(1);
  }
}

